.main {
  min-height: 100vh;
  padding: 4rem 0;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #E9E9E9;
}

.form {
  background-color: #FFF;
  border: 1px solid #ccc;
  border-radius: 10px;
  min-height: 350px;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}

.form div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.form div input {
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: center;
  min-height: 40px;
  min-width: 250px;
  margin: 5px;
}

.form div input:focus {
  outline: none;
}

.form div button {
  min-height: 40px;
  min-width: 150px;
  background-color: #79BD4E;
  border-radius: 5px;
  border: none;
  font-size: .9rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #FFF;
}

.form div button:hover {
  cursor: pointer;
  background-color: #CCC;
}

.form div a {
  font-size: 12px;
  margin: 5px;
}

.form div a:hover {
  color: #79BD4E;
}